<template>
  <section class="banner-layout">
    <div class="banner-layout__main d-flex flex-column align-items-center">
      <router-view />
      <Button type="primary" @click="onReturnHome">
        <Text size="14px" line-height="16px" color="white">
          {{ t('banner.returnHome') }}
        </Text>
      </Button>
    </div>
  </section>
</template>

<script>
import { ROUTES_NAMES } from '@/admin/common/routes'
import { useI18n } from '@/shared/plugins/i18n'
import { useRouter } from 'vue-router'
import Text from '@/shared/components/UIKit/Text/Text'
import Button from '@/shared/components/UIKit/Button'

export default {
  name: 'Banner',
  components: { Text, Button },
  setup () {
    const { t } = useI18n()
    const router = useRouter()

    const onReturnHome = () => {
      router.push({
        name: ROUTES_NAMES.DASHBOARD
      })
    }

    return {
      t,
      onReturnHome
    }
  }
}
</script>

